//(07010010)    (07010020)      (07010030)     (07010040)

export class ProjectCreatePage {
  viewTitle: string;
  type: number;
  public project = {
    id : '',                    //id
    itemName : '',              //
    itemFounder : '',      //
    founderEmpNum : '003169',   //
    leader : '',                  //
    itemLeaderEmpNum :'',       //
    // empNum: '',
    itemCreateTime : '',        //
    itemStartTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),  //
    delayTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),      //
    delayDays : '',             //
    itemUpdate : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),     //
    endTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),        //
    itemVersion : '',           //
    itemLevel : '',             //
    itemStartResult : '',      //
    itemEndResult : '',        //
    children : [],              //
    itemRaise : '',             //
    itemRevision : '',          //
    itemDept : '',              //
    itemState : '',             //
    itemCode : '',              //
    itemWeight : '',            //
    itemProgress : '',          //
    itemIsEnd : false,          //
  };

  constructor(public navCtrl: NavController, public navParams: NavParams,
              public appService : AppService, public toastCtrl: ToastController, public events: Events) {
    var data = this.navParams.get('project');
    this.type = this.navParams.get('type');
    if (data) {
      this.project = data;
      this.viewTitle = this.project.itemName;
    } else {
      this.viewTitle = '';
    }
  }

  ionViewDidLoad() {
    this.events.subscribe('onConfirmProjectLeader',(leader)=>{
        this.project.leader = leader.name;
        this.project.itemLeaderEmpNum = leader.username;
    });
  }

  ionViewWillUnload(){
      this.events.unsubscribe('onConfirmProjectLeader');
  }

  onPublish() {
    // if (this.project.itemName.length < 1) {
    //   let alert = this.alertCtrl.create({
    //     title: '',
    //     subTitle: '!',
    //     buttons: ['']
    //   });
    //   alert.present();
    //   return;
    // }
    // if (this.project.children.length > 0) {
    //   var lastMile = this.project.children[this.project.children.length-1];
    //   if (lastMile.leader.length < 1 || lastMile.deliveryResult.length < 1 || lastMile.itemProgress.length < 1 || lastMile.planTime.length < 1) {
    //     let alert = this.alertCtrl.create({
    //       title: '',
    //       subTitle: '!',
    //       buttons: ['']
    //     });
    //     alert.present();
    //     return;
    //   }
    // }
    this.appService.httpPost("item/createItem", this.project, this, function (view ,res){
      // var data = res.json().data;
      view.events.publish('homeProjectReload');
      let toast = view.toastCtrl.create({
        message: '!',
        duration: 3000
      });
      toast.present();
      view.navCtrl.pop();
    } ,true);
  }

  onAddMilestone() {
    // if (this.project.children.length > 0) {
    //   var lastMile = this.project.children[this.project.children.length-1];
    //   if (lastMile.leader == null || lastMile.deliveryResult == null || lastMile.itemProgress == null || lastMile.planTime  == null) {
    //     let alert = this.alertCtrl.create({
    //         title: '',
    //         subTitle: '!',
    //         buttons: ['']
    //     });
    //     alert.present();
    //     return;
    //   }
    //   if (lastMile.leader.length < 1 || lastMile.deliveryResult.length < 1 || lastMile.itemProgress.length < 1 || lastMile.planTime.length < 1) {
    //     let alert = this.alertCtrl.create({
    //       title: '',
    //       subTitle: '!',
    //       buttons: ['']
    //     });
    //     alert.present();
    //     return;
    //   }
    // }
    var milestone = {
      id : '',                    //id
      milestoneName : ''+(this.project.children.length+1),         //
      leader : '',       //
      leaderEmpNum : '',          //
      // milestoneDelivery : '',
      deliveryResult: '',         //
      itemProgress : '',          //
      deliveryTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),         //
      planTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),              //
      realTime : new DatePipe('en-US').transform(new Date(), 'yyyy-MM-dd'),              //
      remark : '',                //
      isAccomplish : false,       //
      delayDays : 0,              //
      children : [],              //
    };
    // this.project.children.push(milestone);
    this.navCtrl.push(MilestoneDetailPage, {
      milestone : milestone,
      project : this.project,
      type : this.type,
      callback : this.milestoneCallback,
    });
  }

  onLookMilestone() {
    this.navCtrl.push(MilestoneDetailPage, {
      project : this.project
    });
  }

  onClickRemoveMilestone($event, mile) {
    if (this.type == 1) {
      this.deleteOneMile(mile)
    } else {
      this.appService.httpDelete("item/delete",{"ids":mile.id},this,function (view, res) {
        if (res.status == 200) {
            view.deleteOneMile(mile);
        }
      },true);
    }
  }

  deleteOneMile(mile) {
      var deleteId = mile.id;
      var index = -1;
      for (let i=0; i<this.project.children.length; i++) {
          var milestone1 = this.project.children[i];
          if (deleteId == milestone1.id) {
              index = i;
              break;
          }
      }
      if (index >= 0) {
          this.project.children.splice(index, 1);
          for (let i=0; i<this.project.children.length; i++) {
              var milestone2 = this.project.children[i];
              milestone2.milestoneName = ''+(i+1);
          }
      }
  }

  onEndDirector($event) {
    this.navCtrl.push(ContactPage, {
        type: 1,
    });
  }

  milestoneCallback = (milestone) =>
  {
    return new Promise((resolve, reject) => {
      if (typeof (milestone) != 'undefined') {
        var isIn = false;
        for (let i=0; i<this.project.children.length; i++) {
          var tempMile = this.project.children[i];
          if (tempMile.id == milestone.id) {
            isIn = true;
            this.project.children.splice(i, 1, milestone);
            break;
          }
        }
        if (!isIn) {
          this.project.children.push(milestone);
        }
      } else {

      }
      resolve();
    });
  };
}



